import * as React from "react";
import PropTypes from "prop-types";
import { graphql } from "gatsby";

import { OmegaLayout } from "@components/layouts";
import { PostCard, Pagination } from "@components/common";
import { MetaData } from "@components/common/meta";
import SmallDescription from "@components/omega/SmallDescription";

/**
 * Main index page (home page)
 *
 * Loads all posts from Ghost and uses pagination to navigate through them.
 * The number of posts that should appear per page can be setup
 * in /utils/siteConfig.js under `postsPerPage`.
 *
 */
const Blog = ({ data, location, pageContext }) => {
    const posts = data.allGhostPost.edges;

    return (
        <>
            <MetaData location={location} />
            <OmegaLayout isHome={true}>
                <SmallDescription
                    title="My Blog"
                    text={
                        <>
                            Whenever I find something fascinating, I do some
                            research on it &amp; put all of it here.
                        </>
                    }
                />

                <div className="container my-5">
                    <section className="post-feed">
                        {posts
                            .map(({ node }) => node)
                            .sort((a, b) =>
                                a.featured ? -1 : b.featured ? 1 : 0
                            )
                            .map((post) => (
                                <PostCard key={post.id} post={post} />
                            ))}
                    </section>
                    <Pagination pageContext={pageContext} />
                </div>
            </OmegaLayout>
        </>
    );
};

Blog.propTypes = {
    data: PropTypes.shape({
        allGhostPost: PropTypes.object.isRequired,
    }).isRequired,
    location: PropTypes.shape({
        pathname: PropTypes.string.isRequired,
    }).isRequired,
    pageContext: PropTypes.object,
};

export default Blog;

// This page query loads all posts sorted descending by published date
// The `limit` and `skip` values are used for pagination
export const pageQuery = graphql`
    query GhostPostQuery($limit: Int!, $skip: Int!) {
        allGhostPost(
            sort: { order: DESC, fields: [published_at] }
            limit: $limit
            skip: $skip
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }
    }
`;
